html{
    scroll-behavior: smooth;
}

body{
padding: 0;
margin: 0;
}

img{
width: 100%;
height: auto;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}

a,
p,
h1,
h2,
h3,
h4,
ul,
li {
    margin: 0;
    padding: 0;
} 

a{
text-decoration: none;
cursor: pointer;
}

.mainThumb{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #F1F2F5;
    padding-top: 70px;
    padding-bottom: 70px;
    padding-left: 10px;
    padding-right: 10px;
}


.title{
    color: #2B1E18;
text-align: center;
font-family: "Montserrat", sans-serif;
font-size: 44px;
font-style: normal;
font-weight: 700;
line-height: 44px; /* 100% */
letter-spacing: -1.2px;
padding-left: 20px;
padding-right: 20px;
}

.itemName{
    color: #2B1E18;
text-align: center;
font-family: "Montserrat", sans-serif;
font-size: 24px;
font-style: normal;
font-weight: 800;
line-height: 34px; /* 141.667% */
letter-spacing: -0.5px;
}

.itemsThumb{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-width: 1140px;
    gap: 100px;

    @media (min-width:1140px) {
        justify-content: space-between;
        gap: 120px;

    }
    

}

.itemImage{
    width: 70px;
}

.imageThumb{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 49px;
    width: 120px;
    

    @media (min-width: 1140px) {
        margin-top: 61px;
        width: auto;
    }
}